import React from 'react';
import './style.css';
import _ from 'lodash';
import { connect } from 'react-redux';
import BoxGination from '../../components/box-pagination/box-pagination';
import RepresentProject from './represent-project';
import presentBack from '../../assets/images/persent-background.jpg';

import language from '../../modules/language';
import route from '../../config/route';

class screen extends React.Component {
    constructor(props) {
        super(props);
        this.onScroll = _.throttle(this._onScroll, 50);
        this.state = {
            currentPage: 0,
            ...this.renderSubItem(),
        }
        this.lastScrollTime = Date.now();
        this.scrollCount = 0;
        this.threshold = 1;
        this.moveNextPart = _.throttle(this._moveNextPart, 3000,
            { leading: true, trailing: false });
        this.deltaY = 0;
        this.maxPage = 4;
        this.timeoutPart = {};
    }

    steelImages = [
        require('../../assets/images/investment/steel-building-1.jpg'),
        require('../../assets/images/investment/steel-building-2.jpg'),
        require('../../assets/images/investment/steel-building-3.jpg'),
        require('../../assets/images/investment/steel-building-4.jpg'),
    ];

    townhouseImages = [
        require('../../assets/images/investment/townhouse-1.jpg'),
        require('../../assets/images/investment/townhouse-2.jpg'),
        require('../../assets/images/investment/townhouse-3.jpg'),
        require('../../assets/images/investment/townhouse-4.jpg'),
    ];

    designImages = [
        require('../../assets/images/investment/design-1.jpg'),
        require('../../assets/images/investment/design-2.jpg'),
        require('../../assets/images/investment/design-3.jpg'),
        require('../../assets/images/investment/design-4.jpg'),
    ];


    buildingMaterialImages = [
        require('../../assets/images/investment/building-material-1.jpg'),
        require('../../assets/images/investment/building-material-2.jpeg'),
        require('../../assets/images/investment/building-material-3.jpg'),
    ];


    selectBoxPagi(index) {
        if (index !== this.state.currentPage) {
            this.setState({ currentPage: index });

        }

    }


    componentDidMount() {
        window.onwheel = this.onScroll; // modern standard       
        setTimeout(() => {
            if (this.part0 && this.part0.setFocus) {
                this.part0.setFocus(true);
            }
        }, 1000);
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.app.currentLanguage != this.props.app.currentLanguage){
            console.log('Update languege',language.t('investment.steel_building_children'));
            this.setState({
                ...this.renderSubItem(),
            })
        }
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.onScroll);
    }

    renderSubItem() {
        const steelChildren = (language.t('investment.steel_building_children') || [])
            .slice(0, this.steelImages.length)
            .map((item, index) => {
                return ({
                    title: item,
                    image: this.steelImages[index],
                    link: route['SteelBulding' + (1 + index)],
                });
            });

        const townhouseChildren = (language.t('investment.townhouse_children') || [])
            .slice(0, this.townhouseImages.length)
            .map((item, index) => ({
                title: item,
                image: this.townhouseImages[index],
                link: route['SteelBulding' + (1 + index)],
            }));

        const designChildren = (language.t('investment.design_children') || [])
            .slice(0, this.designImages.length)
            .map((item, index) => ({
                title: item,
                image: this.designImages[index],
                link: route['SteelBulding' + (1 + index)],
            }));

        const buildingMaterialChildren = (language.t('investment.building_material_children') || [])
            .slice(0, this.buildingMaterialImages.length)
            .map((item, index) => ({
                title: item,
                image: this.buildingMaterialImages[index],
                link: route['SteelBulding' + (1 + index)],
            }));
        return {
            steelChildren,
            townhouseChildren,
            designChildren,
            buildingMaterialChildren
        };
    }


    _onScroll = e => {
        console.log(e);
        if (Date.now() - this.lastScrollTime < 150) {
            this.scrollCount++;
            this.deltaY += e.deltaY;
            if (this.scrollCount > this.threshold) {
                this.scrollCount = 0;
                this.moveNextPart(this.deltaY < 0);
                this.deltaY = 0;
            }
        } else {
            this.scrollCount = 0;
            this.deltaY = 0;
        }
        this.lastScrollTime = Date.now();
    }

    _moveNextPart = (reverse) => {
        const delta = reverse ? -1 : 1;
        let newPage = this.state.currentPage + delta;
        if (newPage < 0) {
            newPage = 0;
        } else if (newPage >= this.maxPage) {
            newPage = 0;
        }

        this.moveToPart(newPage);
    }

    moveToPart(newPage) {
        console.log('Move to page', newPage);
        this.setState({
            currentPage: newPage,
        });

        //focus page
        for (let i = 0; i < this.maxPage; i++) {
            const part = this['part' + i];
            if (this.timeoutPart[i]) {
                clearTimeout(this.timeoutPart[i]);
            }

            if (i === newPage) {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(true);
                    }
                    this.timeoutPart[i] = undefined;
                }, 1000);
            } else {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(false);
                    }
                    this.timeoutPart[i] = undefined;
                }, 0);
            }
        }
    }

    render() {
        const { currentPage,
            steelChildren,
            buildingMaterialChildren,
            designChildren,
            townhouseChildren,
        } = this.state;

        return (
            <div style={{ overflow: 'hidden', height: '100vh' }} >
                <BoxGination data={Array.from(Array(this.maxPage).keys(), i => i + 1)}
                    currentIndex={currentPage}
                    onChange={(page) => this.moveToPart(page)} />

                <RepresentProject
                    onMouseEnter={() => console.log('onMouseEnter')}
                    onMouseLeave={() => console.log('onMouseLeave')}
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 0 ? '100vh' : '0px',
                        backgroundImage: `url(${presentBack})`,
                        zIndex: 10,
                        overflow: 'hidden',
                    }}
                    title={language.t('investment.steel_building')}
                    description={language.t('investment.steel_building_description')}
                    data={steelChildren}
                    ref={ref => this.part0 = ref}
                />

                <RepresentProject
                    style={{
                        transition: 'all 2s',
                        height: currentPage <= 1 ? '100vh' : '0px',
                        backgroundImage: `url(${presentBack})`,
                        zIndex: 9,
                        overflow: 'hidden',
                    }}
                    title={language.t('investment.townhouse')}
                    description={language.t('investment.townhouse_description')}
                    data={townhouseChildren}
                    ref={ref => this.part1 = ref}
                />

                <RepresentProject
                    style={{
                        transition: 'all 2s',
                        height: currentPage <= 2 ? '100vh' : '0px',
                        backgroundImage: `url(${presentBack})`,
                        zIndex: 8,
                        overflow: 'hidden',
                    }}
                    title={language.t('investment.design')}
                    description={language.t('investment.design_description')}
                    data={designChildren}
                    ref={ref => this.part2 = ref}
                />

                <RepresentProject
                    style={{
                        transition: 'all 2s',
                        height: currentPage <= 3 ? '100vh' : '0px',
                        backgroundImage: `url(${presentBack})`,
                        zIndex: 7,
                        overflow: 'hidden',
                    }}
                    title={language.t('investment.building_material')}
                    description={language.t('investment.building_material_description')}
                    data={buildingMaterialChildren}
                    ref={ref => this.part3 = ref}
                />

            </div>

        )

    }
};

const mapStateToProps = state => ({
    app: state.app,
});

const mapDispatchToProps = {

}

export default connect(mapStateToProps, mapDispatchToProps)(screen);