import React from 'react';
import './style.css';
import PropTypes from 'prop-types';
import { Carousel } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Color } from '../../../config/theme';

export default class RepresentProject extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false,
            hoverIndex: -1,
        }
    }

    setFocus(focus) {
        this.setState({ focus });
    }

    render() {
        const { data, title, description, } = this.props;

        return (
            <div className="represent-project" {...this.props}>
                {/* <div style={{display:"flex", justifyContent:"center"}}> */}
                <div
                    style={{
                        backgroundColor: 'rgb(0,0,0,0.4)',
                        height: "100%",
                        width: "100%",
                        display: "flex",
                    }}
                >
                    <div className="represent-intro">
                        <h2
                            style={{
                                transition: 'all 2s',
                                left: this.state.focus ? '0rem' : '-50rem',
                                position: 'relative'
                            }}
                        >{title}</h2>
                        <div className="represent-content">
                            <div className="represent-slide-show"
                                style={{
                                    transition: 'all 2s',
                                    right: this.state.focus ? '0rem' : '-80rem',
                                    position: 'relative',
                                    transform: this.state.hoverIndex >=0 ? 
                                    'scale(1.1)': undefined,
                                    // border: '#0B5C3B solid'
                                }}
                            >
                                <Carousel
                                    className="full-height"
                                    interval={100000}
                                >
                                    {data.map((item,index) => <Carousel.Item>
                                        <Link to={item.link}
                                        style={{width: '100%'}}>
                                            <div style={{
                                                cursor: 'pointer',
                                                width: '100%'
                                            }}
                                            onMouseEnter={()=>this.setState({hoverIndex: index})}
                                            onMouseLeave={()=>this.setState({hoverIndex: -1})}
                                            >
                                                <div className="d-block w-100"
                                                    style={{
                                                        position: 'relative',
                                                        width: '100%',
                                                        paddingTop: '75%',
                                                        backgroundColor: '#0B5C3B'
                                                    }} >
                                                    <img className="rounded"
                                                        style={{
                                                            position: 'absolute',
                                                            top: 0,
                                                            left: 0,
                                                            right: 0,
                                                            bottom: 0,
                                                            width: '100%',
                                                            resize: 'cover',
                                                        }}
                                                        src={item.image}
                                                        alt="First slide"
                                                    />
                                                </div>

                                                <Carousel.Caption style={{
                                                    borderWidth: 0,
                                                }}>
                                                    <h2 style={{
                                                        fontWeight: 'bold',
                                                        backgroundColor: this.state.hoverIndex == index?'#0B5C3B'
                                                        :'rgb(11, 92, 59, 0.4)',
                                                        paddingTop: '8px',
                                                        paddingLeft: '16px',
                                                        paddingRight: '16px',
                                                    }}>{item.title}</h2>
                                                </Carousel.Caption>
                                            </div>
                                        </Link>
                                    </Carousel.Item>)}
                                </Carousel>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

RepresentProject.propTypes = {
    setFocus: PropTypes.func
}