import React from 'react';
import PropTypes from 'prop-types';
import './style.css';

export default class InfoBox extends React.Component {
    render() {
        let main;
        if (this.props.title) {
            main = [
                <h3
                    style={this.props.titleStyle}
                    className="info-box-title">{this.props.title}</h3>,
                <div className="info-box-content">
                    {this.props.children}
                </div>
            ];
        } else {
            main = this.props.children;
        }
        const borderRadius = this.props.baseRadius || 32;
        return (
            <div
                style={{
                    ...this.props.style,
                    borderRadius: borderRadius + 'px',
                }}
                className="w-100 h-100 info-box-container px-2 py-2">
                <div className="w-100 h-100 info-box-sub-container"
                style={{

                    borderRadius: borderRadius - 2 + 'px',
                }}>
                    <div className="px-3 py-3 text-container"
                    style={{
                        borderRadius: borderRadius - 4 + 'px',
                    }}>
                    {main}

                </div>
            </div>
            </div >
        );
    }
}

InfoBox.propTypes = {
    style: PropTypes.object,
    title: PropTypes.string,
    titleStyle: PropTypes.object,
}