import React from 'react';
import PropTypes from 'prop-types';
import './style.css';
import hexagon from '../../assets/images/suitstain/hexagonal.png';

export default class SuitstainDevContent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false,
            activeItemIndex: 0,
        }
        this.maxItem = 7;
    }

    setFocus(focus) {
        this.setState({ focus })
    }

    render() {
        const {focus} = this.state;
        const { title, image } = this.props;
        return (
            <div style={{
                ...this.props.style,
                backgroundPosition: "center",
                backgroundSize: "cover",
            }}>
                <div className="suitstain-container">
                    <div
                        className="suistain-hexagon"
                    >
                        <img
                            style={{
                                transition: 'all 1s',
                                transitionDelay: '2s',
                                opacity: focus ? .1 : 0
                            }} 
                            src={hexagon} 
                        />
                        <img
                        style={{
                            transition: 'all 1.5s',
                            transitionDelay: '2s',
                            opacity: focus ? .1 : 0
                        }} 
                        src={hexagon} />
                        <img 
                        style={{
                            transition: 'all 2s',
                            transitionDelay: '2s',
                            opacity: focus ? 0.1 : 0
                        }} 
                        src={hexagon} />
                        <img 
                        style={{
                            transition: 'all 2.5s',
                            transitionDelay: '2s',
                            opacity: focus ? 0.1 : 0
                        }} 
                        src={hexagon} />

                    </div>
                    <h2
                        style={{
                            transition: 'all 2s',
                            opacity: focus ? 1 : 0,
                            bottom: focus ? '0px' : '-400px'
                        }}
                    >{title}</h2>
                    <div className="row mx-0 my-0">
                        <div className="col-md-6">
                            <div
                                className="suitstain-img"
                                style={{
                                    transition: 'all 2s',
                                    opacity: focus ? 1 : 0
                                }}
                            >
                                <img src={image} />

                            </div>
                        </div>
                        <div
                            style={{
                                transition: 'all 2s',
                                opacity: focus ? 1 : 0,
                                // position:'absolute',
                                bottom: focus ? '0px' : '-400px',
                                margin: 'auto'
                            }}
                            className="col-md-6">
                            {this.props.children}
                        </div>
                        <div className="suistain-text-bgr">

                        </div>
                    </div>
                </div>
            </div>
        )
    }

}

SuitstainDevContent.protoTypes = {
    setFocus: PropTypes.func
}