import React from 'react';
import IdeaPart from './idea-part';
import './style.css';
import _ from 'lodash';
import SlideShow from '../../components/slide-show/slide-show';
import BoxGination from '../../components/box-pagination/box-pagination';
import Navigation from '../../components/navigation/navigation';
import Cutural from '../../components/cutural/cutural';
import RepresentProject from '../../components/represent-project/represent-project';
import slideShow1 from '../../assets/images/home-slide-show-1.jpg';
import slideShow2 from '../../assets/images/home-slide-show-2.jpg';
import mainCerti from '../../assets/images/home-certi-main.png';
import homeSubCerti1 from '../../assets/images/home-certi-1.png';
import homeSubCerti2 from '../../assets/images/home-certi-2.png';
import homeSubCerti3 from '../../assets/images/home-certi-3.png';
import homeSubCerti4 from '../../assets/images/home-certi-4.png';
import homeSubCerti5 from '../../assets/images/home-certi-5.png';
import presentBack from '../../assets/images/persent-background.jpg';
import present1 from '../../assets/images/present1.jpg';
import present2 from '../../assets/images/present2.jpg';
import present3 from '../../assets/images/present3.jpg';
import present4 from '../../assets/images/present4.jpg';
import present5 from '../../assets/images/present5.jpg';
import StatisticPart from './statistic-part';
// import { url } from 'inspector';


export default class Home extends React.Component {
    constructor(props) {
        super(props);
        this.onScroll = _.throttle(this._onScroll, 50);
        this.state = {
            currentPage: 0
        }
        this.lastScrollTime = Date.now();
        this.scrollCount = 0;
        this.threshold = 1;
        this.moveNextPart = _.throttle(this._moveNextPart, 3000,
            { leading: true, trailing: false });
        this.deltaY = 0;
        this.maxPage = 4;
        this.timeoutPart = {};
    }

    selectBoxPagi(index) {
        if (index !== this.state.currentPage) {
            this.setState({ currentPage: index });

        }

    }


    // componentDidMount(){

    // }

    componentDidMount() {
        window.onwheel = this.onScroll; // modern standard       
        setTimeout(() => {
            if (this.part0 && this.part0.setFocus) {
                this.part0.setFocus(true);
            }
        }, 1000);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.onScroll);
    }


    _onScroll = e => {
        console.log(e);
        if (Date.now() - this.lastScrollTime < 150) {
            this.scrollCount++;
            this.deltaY += e.deltaY;
            if (this.scrollCount > this.threshold) {
                this.scrollCount = 0;
                this.moveNextPart(this.deltaY < 0);
                this.deltaY = 0;
            }
        } else {
            this.scrollCount = 0;
            this.deltaY = 0;
        }
        this.lastScrollTime = Date.now();
    }

    _moveNextPart = (reverse) => {
        const delta = reverse ? -1 : 1;
        let newPage = this.state.currentPage + delta;
        if (newPage < 0) {
            newPage = 0;
        } else if (newPage >= this.maxPage) {
            newPage = 0;
        }

        this.moveToPart(newPage);
    }

    moveToPart(newPage) {
        console.log('Move to page', newPage);
        this.setState({
            currentPage: newPage,
        });

        //focus page
        for (let i = 0; i < this.maxPage; i++) {
            const part = this['part' + i];
            if (this.timeoutPart[i]) {
                clearTimeout(this.timeoutPart[i]);
            }

            if (i === newPage) {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(true);
                    }
                    this.timeoutPart[i] = undefined;
                }, 1000);
            } else {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(false);
                    }
                    this.timeoutPart[i] = undefined;
                }, 0);
            }
        }
    }

    render() {
        const { currentPage } = this.state;
        return (
            <div style={{ overflow: 'hidden', height: '100vh' }}>
                <BoxGination data={Array.from(Array(this.maxPage).keys(), i => i + 1)}
                    currentIndex={currentPage}
                    onChange={(page) => this.moveToPart(page)} />

                <SlideShow style={{
                    transition: 'all 2s',
                    height: currentPage == 0 ? '100vh' : '0px',
                    overflow: 'hidden',
                }}
                    data={[slideShow1, slideShow2]}
                    activeBanner={currentPage == 0 ? true : false}
                    mainCerti={mainCerti}
                    subCerti={[homeSubCerti1, homeSubCerti2, homeSubCerti3, homeSubCerti4, homeSubCerti5]}
                    focus={true}
                    fade={true}
                    ref={ref => this.part0 = ref}
                />

                <IdeaPart style={{
                    transition: 'all 2s',
                    height: currentPage == 1 ? '100vh' : '0px',
                    overflow: 'hidden',
                }}
                    ref={ref => this.part1 = ref}
                />
                <RepresentProject
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 2 ? '100vh' : '0px',
                        backgroundImage: `url(${presentBack})`,
                        zIndex: 10,
                        overflow: 'hidden',
                        display: "flex",
                        overflow: 'hidden',
                    }}
                    data={[present1, present2, present3, present4, present5]}
                    ref={ref => this.part2 = ref}
                />
                <StatisticPart style={{
                    transition: 'all 2s',
                    height: currentPage == 3 ? '100vh' : '0px',
                    overflow: 'hidden',
                }}
                    ref={ref => this.part3 = ref}
                />
            </div>

        )

    }
}