import React from 'react';
import './style.css';
import {connect} from 'react-redux';
import AppAction from '../../actions/app';
import route from '../../config/route';
import {Link, withRouter} from 'react-router-dom';
import {
    Dropdown,
} from 'react-bootstrap';
import language from '../../modules/language';

export function NavigationHolder(props){
    return (
        <div className="sub-navigation"
        style={{
            zIndex: '-1 !important',
        }}></div>
    );
}

class Navigation extends React.Component{
    constructor(props){
        super(props);
    }

  
    render(){
        const {data, activeIndex} = this.props;
        return [
            <div style={this.props.style} key={'nav-1'} className="col-12 row
            justify-content-center align-items-center sub-navigation position-fixed">
                <ul className="nav">
                {data.map((item,index)=>
                    <li className={`nav-item sub-nav-item ${index == activeIndex?'sub-nav-active':''}`}>
                        <a className="nav-link"
                        href={item.link} onClick={item.onClick}>{item.title}</a>
                    </li>
                    )}
                </ul>
            </div>,
           
        ];
    }
}

const mapStateToProps = state=>({
    app: state.app,
});

const mapDispatchToProps = {
    showMenu: AppAction.showMenu,
    hideMenu: AppAction.hideMenu,
    changeLanguage: AppAction.changeLanguage,
}

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Navigation));