import React from 'react';
import './style.css';
import PropTypes from 'prop-types';
import { Carousel } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Color } from '../../../config/theme';

export default class RepresentProject extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false
        }
    }

    setFocus(focus) {
        this.setState({ focus });
    }

    render() {
        const { data, title, description, } = this.props;

        return (
            <div className="represent-project" style={this.props.style}>
                {/* <div style={{display:"flex", justifyContent:"center"}}> */}
                <div
                    style={{
                        backgroundColor: 'rgb(0,0,0,0.4)',
                        height: "100%",
                        width: "100%",
                        display: "flex"

                    }}
                >
                    <div className="represent-intro">
                        <h2
                            style={{
                                transition: 'all 2s',
                                left: this.state.focus ? '0rem' : '-50rem',
                                position: 'relative'
                            }}
                        >{title}</h2>
                        <div className="represent-content">

                            <div className="represent-intro-text"
                                style={{
                                    transition: 'all 2s',
                                    bottom: this.state.focus ? '0rem' : '-50rem',
                                    position: 'relative',
                                    height: 'inherit',
                                    minHeight: '200px',
                                    marginTop: '30px'
                                }}
                            >
                                <div className="represent-text"
                                style={{
                                    backgroundColor: 'rgb(11, 92, 59, 0.8)',
                                    minHeight: '200px',
                                }}>
                                    {description}
                                </div>
                            </div>
                            <div className="represent-slide-show"
                                style={{
                                    transition: 'all 2s',
                                    right: this.state.focus ? '0rem' : '-80rem',
                                    position: 'relative'
                                }}
                            >
                                <Carousel
                                    className="full-height"
                                // activeIndex={index}
                                // direction={direction}
                                // onSelect={this.handleSelect}
                                // fade={this.props.fade ? this.props.fade : false}
                                >
                                    {data.map(item => <Carousel.Item>
                                        <Link to={item.link}
                                        style={{width: '100%'}}>
                                            <div style={{
                                                cursor: 'pointer',
                                                width: '100%'
                                            }}>
                                                <div className="d-block w-100"
                                                    style={{
                                                        position: 'relative',
                                                        width: '100%',
                                                        paddingTop: '75%',
                                                    }} >
                                                    <img
                                                        style={{
                                                            position: 'absolute',
                                                            top: 0,
                                                            left: 0,
                                                            right: 0,
                                                            bottom: 0,
                                                            resize: 'cover'
                                                        }}
                                                        src={item.image}
                                                        alt="First slide"
                                                    />
                                                </div>

                                                <Carousel.Caption style={{
                                                    borderWidth: 0,
                                                }}>
                                                    <h2 style={{
                                                        fontWeight: 'bold',
                                                        backgroundColor: 'rgb(11, 92, 59, 0.4)',
                                                        paddingTop: '8px',
                                                        paddingLeft: '16px',
                                                        paddingRight: '16px',
                                                    }}>{item.title}</h2>
                                                </Carousel.Caption>
                                            </div>
                                        </Link>
                                    </Carousel.Item>)}
                                </Carousel>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

RepresentProject.propTypes = {
    setFocus: PropTypes.func
}