import React from 'react';
import './style.css';


export default class BoxGination extends React.Component {
    onSelect(index) {
        if (this.props.onChange) {
            this.props.onChange(index);
        }
    }

    render() {
        let { data, currentIndex } = this.props;
        let paginations = [];
        for (let index = 0; index < data.length; index++) {
            let page = index < 10 ? "0" + (index+1)  : (index+1);
            let currentClass = index === currentIndex ? "currentIndex" : "noneIndex";
            paginations.push(
                <li className={currentClass} key={index} onClick={() => this.onSelect(index)}>
                    <a><span>{page}</span></a>
                </li>
            )
        }
        return (
            <div  className={"paginationBox"}>
                <ul>
                    {paginations}
                </ul>
            </div>
        );
    }
}