
import language from '../modules/language';

export const Types = {
    changeMenuState: 'app.change-menu-state',
    changeField: 'app.change-field',
};

export const createAction = (type, payload, message) => {
    return {
        type,
        payload,
        message,
    };
}

export const hideMenu = () => createAction(Types.changeMenuState, false);

export const showMenu = () => createAction(Types.changeMenuState, true);


export const changeLanguage = (value) => {
    language.setLanguage(value.code);
    return createAction(Types.changeField, { key: 'currentLanguage', value });
}

export default {
    createAction,
    hideMenu,
    showMenu,
    changeLanguage,
}
