import React from 'react';
import PropTypes from 'prop-types';
import InfoBox from '../../components/info-box';
import ItemsCarousel from 'react-items-carousel';

export default class Investment extends React.Component {
    state = {
        focus: false,
        activeItemIndex: 0,
    }
    maxItem = 7;
    subItems = [
        {title: '2', description: 'Nhà máy'},
        {title: '500+', description: 'Công nhân công trình'},
        {title: '800', description: 'Dự án sản xuất & xuất khẩu'},
        {title: '900.000', description: 'Giờ thi công an toàn'},
        {title: '550+', description: 'Kỹ sư, nhân viên'},
        {title: '50.000', description: 'Năng lực (tấn/năm)'},
        {title: '7.000+', description: 'Dự án đã thiết kế'},
    ]

    setFocus(focus) {
        this.setState({ focus });
    }

    componentDidMount(){
        this.interval = setInterval(()=>{
            this.setState({
                activeItemIndex: this.state.activeItemIndex >= this.maxItem -1 ?
                0: this.state.activeItemIndex + 1,
            });
        },2000);
    }
    
    componentWillUnmount(){
        clearInterval(this.interval);
    }

    render() {
        return (
            <div style={{
                ...this.props.style,
                backgroundImage: `url(${require('../../assets/images/kho-van-quan.jpg')})`,
                backgroundRepeat: 'no-repeat',
                backgroundSize: '100% 100%',
                position: 'relative',
            }}
                className={`home-statistic row justify-content-center
                    align-items-center ${
                    this.state.focus ? 'home-statistic-focus' : ''}`
                }
            >
                <div style={{
                    backgroundColor: 'rgb(0,0,0,0.8)',
                    height: "100%",
                    width: "100%"
                }}>
                <div className="row"
                    style={{
                        transition: 'all 2s',
                        bottom: this.state.focus?'5em':'-35em',
                        // display: this.state.focus?'block':'none',
                        width: '35em', height: '25em',
                        position: 'absolute', right: '5em',
                    }}>
                    <InfoBox
                        titleStyle={{textAlign: 'center'}}
                        title={'Thống kê nổi bật'}>
                        <div className="col-12 row mx-0 px-0 pt-4">
                            <div className="col-4 row mx-0 justify-content-center statistic-item">
                                <label className="text-center 
                                statistic-title">
                                    15+ </label>
                                <label className="text-center
                                 statistic-description w-100">
                                    Thành lập hơn 15 năm</label>
                            </div>
                            <div className="col-4 row mx-0 justify-content-center statistic-item">
                                <label className="text-center 
                                statistic-title">
                                    600+</label>
                                <label className="text-center
                                 statistic-description w-100">
                                    Dự án hoàn thành</label>
                            </div>
                            <div className="col-4 row mx-0 justify-content-center statistic-item">
                                <label className="text-center 
                                statistic-title">
                                    90%</label>
                                <label className="text-center
                                 statistic-description w-100">
                                    Khách hàng hài lòng</label>
                            </div>
                        </div>
                        <div className="col-12 pt-5">
                        <ItemsCarousel
                            requestToChangeActive={(item)=>this.setState({activeItemIndex: item})}
                            activeItemIndex={this.state.activeItemIndex}
                            numberOfCards={4}
                            gutter={20}
                            outsideChevron
                        >
                            {this.subItems.map(item=>
                                <div style={{ 
                                height: 100, 
                                }}
                                className="sub-statistic-item">
                                    
                                <div className="sub-statistic-title
                                text-center w-100">{item.title}</div>
                                <div className="sub-statistic-description
                                text-center w-100 pt-2">{item.description}</div>
                            </div>
                                )}
    

                            </ItemsCarousel>
                        </div>
                    </InfoBox>
                </div>
                </div>
            </div>
        );
    }
}

Investment.propTypes = {
    setFocus: PropTypes.func
} 