import React from 'react';
import './style.css';
import PropTypes from 'prop-types';


export default class StructCompany extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false
        }
    }
    setFocus(focus) {
        this.setState({ focus })
    }

    render() {
        const { focus } = this.state;
        return (
            <div style={{ ...this.props.style }}>
                <div style={{
                    backgroundColor: 'rgba(111,111,111, 0.3)',
                    display:'flex',
                    width: '100%'
                }}>
                <div className="struct-company-container">
                    <h2
                        style={{
                            transition: 'all 0.8s',
                            opacity: focus ? 1 : 0,
                        }}
                    >Cơ cấu tổ chức</h2>
                </div>
                </div>
            </div>

        )
    }

}

StructCompany.propTypes = {
    setFocus: PropTypes.func
}