import React from 'react';
import PropTypes from 'prop-types';
import './style.css';

export default class InfoBox extends React.Component {
    render() {
        return (
            <div
                style={this.props.style}
                className="w-100 h-100 news-box-container px-2 py-2">
                <div className="w-100 h-100 news-box-sub-container"
                    style={{ position: 'relative' }}>
                    <div className="h-100 px-3 py-3 news-box-text-container"
                        style={{
                            position: 'absolute',
                            right: '-1em',
                            left: '0px',
                        }}>
                        {this.props.children}

                    </div>
                </div>
            </div>
        );
    }
}

InfoBox.propTypes = {
    style: PropTypes.object,
    title: PropTypes.string,
    titleStyle: PropTypes.object,
}