import React from 'react';
import './style.css';
import _ from 'lodash';
import { connect } from 'react-redux';
import BoxGination from '../../components/box-pagination/box-pagination';
import RepresentProject from './represent-project';
import presentBack from '../../assets/images/persent-background.jpg';

import language from '../../modules/language';
import route from '../../config/route';
import {Color} from '../../config/theme';

class screen extends React.Component {
    constructor(props) {
        super(props);
        this.onScroll = _.throttle(this._onScroll, 50);
        this.state = {
            currentPage: 0,
        }
        this.lastScrollTime = Date.now();
        this.scrollCount = 0;
        this.threshold = 1;
        this.moveNextPart = _.throttle(this._moveNextPart, 3000,
            { leading: true, trailing: false });
        this.deltaY = 0;
        this.maxPage = 2;
        this.timeoutPart = {};
    }

    selectBoxPagi(index) {
        if (index !== this.state.currentPage) {
            this.setState({ currentPage: index });
        }
    }


    componentDidMount() {
        window.onwheel = this.onScroll; // modern standard       
        setTimeout(() => {
            if (this.part0 && this.part0.setFocus) {
                this.part0.setFocus(true);
            }
        }, 1000);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.app.currentLanguage != this.props.app.currentLanguage) {
            console.log('Update languege', language.t('investment.steel_building_children'));
            this.setState({
                ...this.renderSubItem(),
            })
        }
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.onScroll);
    }



    _onScroll = e => {
        console.log(e);
        if (Date.now() - this.lastScrollTime < 150) {
            this.scrollCount++;
            this.deltaY += e.deltaY;
            if (this.scrollCount > this.threshold) {
                this.scrollCount = 0;
                this.moveNextPart(this.deltaY < 0);
                this.deltaY = 0;
            }
        } else {
            this.scrollCount = 0;
            this.deltaY = 0;
        }
        this.lastScrollTime = Date.now();
    }

    _moveNextPart = (reverse) => {
        const delta = reverse ? -1 : 1;
        let newPage = this.state.currentPage + delta;
        if (newPage < 0) {
            newPage = 0;
        } else if (newPage >= this.maxPage) {
            newPage = 0;
        }

        this.moveToPart(newPage);
    }

    moveToPart(newPage) {
        console.log('Move to page', newPage);
        this.setState({
            currentPage: newPage,
        });

        //focus page
        for (let i = 0; i < this.maxPage; i++) {
            const part = this['part' + i];
            if (this.timeoutPart[i]) {
                clearTimeout(this.timeoutPart[i]);
            }

            if (i === newPage) {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(true);
                    }
                    this.timeoutPart[i] = undefined;
                }, 1000);
            } else {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(false);
                    }
                    this.timeoutPart[i] = undefined;
                }, 0);
            }
        }
    }

    render() {
        const { currentPage,
        } = this.state;

        return (
            <div style={{ overflow: 'hidden', height: '100vh' }} >
                <BoxGination data={Array.from(Array(this.maxPage).keys(), i => i + 1)}
                    currentIndex={currentPage}
                    onChange={(page) => this.moveToPart(page)} />

                <RepresentProject
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 0 ? '100vh' : '0px',
                        backgroundImage: `url(${presentBack})`,
                        zIndex: 10,
                        overflow: 'hidden',
                    }}
                    title={language.t('investment.steel_building')}
                    description={language.t('investment.steel_building_description')}
                    data={language.t('contact.office') || []}
                    ref={ref => this.part0 = ref}
                />

                <div
                    style={{
                        width: '100%',
                        transition: 'all 2s',
                        height: currentPage <= 1 ? '100vh' : '0px',
                        backgroundImage: `url(${presentBack})`,
                        zIndex: 9,
                        overflow: 'hidden',
                    }}
                    ref={ref => this.part1 = ref}
                >
                    <div className="col-12 h-100 row justify-content-center align-items-center">
                        <div className="col-8 py-5" style={{
                            backgroundColor: 'rgba(0,0,0,0.5)',
                            borderRadius: '8px',
                        }}>
                            <h4 className="text-white text-center">{language.t('contact.title')}</h4>
                            <div className="form-row">
                                <div className="form-group col-md-6">
                                    <label className="text-white" for="name">{language.t('contact.fullname')}</label>
                                    <input type="name" className="form-control" id="name" placeholder={language.t('contact.fullname')} />
                                </div>
                                <div className="form-group col-md-6">
                                    <label className="text-white" for="email">{language.t('contact.email')}</label>
                                    <input type="email" className="form-control" id="email" placeholder={language.t('contact.email')} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-6">
                                    <label className="text-white" for="phone">{language.t('contact.phone')}</label>
                                    <input type="text" className="form-control" id="phone" placeholder={language.t('contact.phone')} />
                                </div>
                                <div className="form-group col-md-6">
                                    <label className="text-white" for="address">{language.t('contact.address')}</label>
                                    <input type="text" className="form-control" id="address" placeholder={language.t('contact.address')}/>
                                </div>
                            </div>
                            <div className="form">
                                <label className="text-white" for="content">{language.t('contact.content')}</label>
                                <textarea class="form-control" 
                                id="content"
                                 placeholder={language.t('contact.content_placeholder')} required></textarea>
                            </div>
                            <div className="col-12 row justify-content-center my-3">
                                <button className="btn px-3" style={{
                                    backgroundColor: Color.primaryColor,
                                    minWidth: '110px',
                                    color: 'white',
                                }}>{language.t('contact.submit')}</button>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

        )

    }
};

const mapStateToProps = state => ({
    app: state.app,
});

const mapDispatchToProps = {

}

export default connect(mapStateToProps, mapDispatchToProps)(screen);