import React from 'react';
import './style.css';
import _ from 'lodash';
import { connect } from 'react-redux';
import { NavigationHolder } from '../../components/navigation';
import language from '../../modules/language';
import route from '../../config/route';

class screen extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showTitle: false,
            showContainer: false,
        }
    }

    partners = [
        [
            {
                image: require('../../assets/images/partner/vietcombank.jpg'),
                link: 'https://www.vietcombank.com.vn',
            },
            {
                image: require('../../assets/images/partner/acb.jpg'),
                link: 'https://www.vietcombank.com.vn',
            },
            {
                image: require('../../assets/images/partner/vietbank.jpg'),
                link: 'https://www.vietcombank.com.vn',
            },
            {
                image: require('../../assets/images/partner/sacombank.jpg'),
                link: 'https://www.vietcombank.com.vn',
            },
        ],
        [
            {
                image: require('../../assets/images/partner/triviet.jpg'),
                link: 'https://www.vietcombank.com.vn',
            },
            {
                image: require('../../assets/images/partner/cotecons.jpg'),
                link: 'https://www.vietcombank.com.vn',
            },
            {
                image: require('../../assets/images/partner/logo-1.png'),
                link: 'https://www.vietcombank.com.vn',
            },
            {
                image: require('../../assets/images/partner/queen-viet.png'),
                link: 'https://www.vietcombank.com.vn',
            },
        ],
        [
            {
                image: require('../../assets/images/partner/cnt.jpg'),
                link: 'https://www.vietcombank.com.vn',
            },
            {
                image: require('../../assets/images/partner/xuan-trung-phat.png'),
                link: 'https://www.vietcombank.com.vn',
            }
        ]
    ];

    componentDidMount() {
        setTimeout(() => {
            this.setState({ showTitle: true });
        }, 1000);
        setTimeout(() => {
            this.setState({ showContainer: true });
        }, 1500);
    }

    render() {
        return (
            <div style={{
                overflow: 'hidden',
                width: '100vw',
                height: '100vh',
                background: `url(${require('../../assets/images/partner/bg.jpg')})`,
            }} >
                <NavigationHolder />

                <div
                    style={{
                        opacity: this.state.showTitle ? 1 : 0,
                        marginTop: this.state.showTitle ? '5em' : '15em',
                        transition: 'all 2s' /* Animation */
                    }}>
                    <h2 className="text-center text-white">{language.t('partner.title')}</h2>
                    <div className="partner-separator"></div>
                    <div style={{
                        maxHeight: '70vh',
                        overflowY: 'scroll',
                        opacity: this.state.showContainer ? 1 : 0,
                        transition: 'all 2s' /* Animation */
                    }}>
                        {this.partners.map(item =>
                            <div className="col-12 justify-content-center row partner-container">
                                {item.map(partner =>
                                    <a href={partner.link} target="_blank">
                                        <div className="partner-item">
                                            <img src={partner.image}
                                                width="300px" height="180px"
                                                style={{
                                                    width: '300px',
                                                    height: '180px',
                                                    resize: 'contain'
                                                }}
                                                alt={item.link} />
                                        </div>
                                    </a>
                                )}
                            </div>
                        )}
                    </div>
                </div>

            </div>

        )

    }
};

const mapStateToProps = state => ({
    app: state.app,
});

const mapDispatchToProps = {

}

export default connect(mapStateToProps, mapDispatchToProps)(screen);