import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

// import logo from './logo.svg';
import './App.css';
import './assets/css/style.css';
import { Provider } from 'react-redux';
import store from './store';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import route from './config/route';
import Navigation from './components/navigation';
import Introduction from './screens/introduction';
import Home from './screens/home';
import Project from './screens/project';
import News from './screens/news';
import Investment from './screens/investment';
import InvestmentDetail from './screens/investment-detail';
import Partner from './screens/partner';
import Resource from './screens/resource';
import SuitstainDev from './screens/suitstainDev';
import Contact from './screens/contact';
// import Library from './screens/library';

function App() {
  return (
    <Provider store={store}>
      <Router>
        <div className="w-100 h-100 px-0" style={{
          // overflowY: 'scroll',
        }}>
          <Navigation />
          <Switch>
            <Route path={route.Project} component={Project} />
            <Route path={route.News} component={News} />
            <Route path={route.CustomerService} component={News} />
            {/* <Route path={route.Intro} component={Introduction} /> */}
            <Route path={route.SustainableDevelopment} component={SuitstainDev} />
            <Route path={route.InvestmentActivity} component={Investment} />
            <Route path={route.SteelBulding1}
              component={InvestmentDetail.SteelBuilding1} />
            <Route path={route.SteelBulding2}
              component={InvestmentDetail.SteelBuilding2} />
            <Route path={route.SteelBulding3}
              component={InvestmentDetail.SteelBuilding3} />
            <Route path={route.SteelBulding4}
              component={InvestmentDetail.SteelBuilding4} />

            <Route path={route.Resource}
              component={Resource} />    
            <Route path={route.Partner}
            component={Partner}/>
            <Route path={route.Contact}
            component={Contact}/>

            <Route path={route.Home} component={Introduction} />
          </Switch>
        </div>
      </Router>
    </Provider>
  );
}

export default App;
