import React from 'react';
import './style.css';
import PropTypes from 'prop-types';
import { Carousel } from 'react-bootstrap';
import TextBox from '../../../components/text-box/textBox';
import profile from '../../../assets/images/introduction/profile.jpg';


export default class LeaderClan extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false,
            clans: ['Hội đồng quản trị', 'Ban điều hành'],
            clanIndex: 0,
            direction: null
        }
    }
    setFocus(focus) {
        this.setState({ focus })
    }

    componentDidMount() {
        if (this.state.focus) {
            this.setState({ clanIndex: 0 })
        }
    }

    selectClan(index) {
        if (index !== this.state.clanIndex) {
            this.setState({ clanIndex: index });
        }
    }

    handleSelect = (index, direct) => {
        if (index !== this.state.clanIndex) {
            this.setState({ clanIndex: index });
        }
        if (direct !== this.state.direction) {
            this.setState({ direction: direct });
        }
    }

    render() {
        const { focus, clans, clanIndex, direction } = this.state;
        let sidebar = [];
        for (let i = 0; i < clans.length; i++) {
            sidebar.push(
                <li
                    key={i}
                    onClick={() => this.selectClan(i)}
                    className={i === clanIndex ? 'leader-clan-active' : ''}
                >
                    {clans[i]}
                </li>
            )
        }
        return (
            <div style={{ ...this.props.style }}>
                <div style={{
                    backgroundColor: "rgb(0,0,0,0.3)",
                    display: 'flex',
                    height: "100%",
                    width: "100%"
                }}>
                    <div className="leader-clan-container">
                        <div className="leader-clan-sidebar"
                            style={{
                                transition: 'all 2s',
                                opacity: focus ? 1 : 0,
                                left: focus ? '0' : '-100px'
                            }}
                        >
                            <ul>
                                {sidebar}
                            </ul>
                        </div>
                        <h2
                            style={{
                                transition: 'all 2s',
                                opacity: focus ? 1 : 0,
                            }}
                        >Ban lãnh đạo</h2>
                        <div className="leader-clan-slider"
                            style={{
                                transition: 'all 2s',
                                position: 'relative',
                                opacity: focus ? 1 : 0,
                                bottom: focus ? '0' : '-500px'
                                // bottom: focus ? '' : '-500px',

                            }}
                        >
                            <Carousel
                                style={{ width: "100%", padding: "10px" }}
                                activeIndex={clanIndex}
                                direction={direction}
                                onSelect={this.handleSelect}
                                interval={null}
                            >
                                <Carousel.Item>
                                    <div className="row mx-0 my-0">
                                        <div className="col-md-12 mb-3">
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>
                                        <div className="col-md-4" >
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>

                                        <div className="col-md-4" >
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>

                                        <div className="col-md-4" >
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>
                                    </div>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <div className="row mx-0 my-0">
                                        <div className="col-md-12 mb-3">
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>
                                        <div className="col-md-4" >
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>

                                        <div className="col-md-4" >
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>

                                        <div className="col-md-4" >
                                            <div className="leader-clan-profile">
                                                <img src={profile} />
                                            </div>
                                            <h4>Ông Vũ Thái Nguyên</h4>
                                            <p>Chủ tịch hội đồng quản trị</p>
                                        </div>
                                    </div>
                                </Carousel.Item>
                            </Carousel>
                        </div>
                    </div>
                </div>
            </div>

        )
    }

}

LeaderClan.propTypes = {
    setFocus: PropTypes.func
}